<template>
	<div class="news_page">
		<ul>
			<li :style="{opacity}">News</li>
			<li><input type="text"></li>
			<li><input type="text"></li>
			<li><input type="text"></li>
		</ul>
	</div>
</template>

<script>
	export default {
		name:'News',
		data() {
			return{
				opacity:1
			}
		},
		activated() {
			this.timer = setInterval(()=>{
				this.opacity -= 0.01;
				if(this.opacity <= 0) this.opacity = 1;
			},10)
		},
		deactivated() {
			clearInterval(this.timer)
		}
	}
</script>

