<template>
	<div class="indexBody">
		<el-button type="success" @click="open">点击获取权限</el-button>
		<el-date-picker
		      v-model="value2"
		      align="right"
		      type="date"
		      placeholder="选择日期"
		      :picker-options="pickerOptions">
		</el-date-picker>
		<el-button type="text">{{value2}}</el-button>
		<div class="navList">
			<ul>
				<!-- Vue借助router-link 实现页面切换 -->
				<li><router-link active-class="active" to="/home">Home</router-link></li>
				<li><router-link active-class="active" to="/about">About</router-link></li>
			</ul>
		</div>
		<div class="panel">
			<div class="container">
				<!-- Vue借助router-view 实现页面显示 -->
				<router-view></router-view>
			</div>
		</div>
	</div>
</template>

<script>
export default {
	name: 'App',
	data() {
        return {
			pickerOptions: {
				disabledDate(time) {
				return time.getTime() > Date.now();
            },
            shortcuts: [{
				text: '今天',
				onClick(picker) {
					picker.$emit('pick', new Date());
				}
            },
			{
				text: '昨天',
				onClick(picker) {
					const date = new Date();
					date.setTime(date.getTime() - 3600 * 1000 * 24);
					picker.$emit('pick', date);
			}
            },
			{
				text: '一周前',
				onClick(picker) {
					const date = new Date();
					date.setTime(date.getTime() - 3600 * 1000 * 24 * 7);
					picker.$emit('pick', date);
				}
            }]
          },
          value1: '',
          value2: '',
        }
    },
	methods: {
	  open() {
	    this.$prompt('请输入邮箱', '提示', {
	      confirmButtonText: '确定',
	      cancelButtonText: '取消',
	      inputPattern: /[\w!#$%&'*+/=?^_`{|}~-]+(?:\.[\w!#$%&'*+/=?^_`{|}~-]+)*@(?:[\w](?:[\w-]*[\w])?\.)+[\w](?:[\w-]*[\w])?/,
	      inputErrorMessage: '邮箱格式不正确'
	    }).then(({ value }) => {
	      this.$message({
	        type: 'success',
	        message: '你的邮箱是: ' + value
	      });
	    }).catch(() => {
	      this.$message({
	        type: 'info',
	        message: '取消输入'
	      });       
	    });
	  }
	}
}
</script>

<style>
	.navList{}
	.navList ul{display: flex;justify-content: flex-start;align-items: center;}
	.navList li{list-style-type: none;}
	.navList li a{display: block;color: #000;text-decoration: none;width: 200px;text-align: center;margin: 5px;padding: 10px 0px;border: 1px solid #eee;}
	.navList .active{background: #000;color: #fff;}
</style>
