<template>
	<div class="message_page">
		<div class="go">
			<ul>
				<li><button @click="back">后退</button></li>
				<li><button @click="forward">前进</button></li>
			</ul>
		</div>
		<div class="messageList">
			<ul>
				<li v-for="info in userInfo" :key="info.id">
					<!-- query字符串形式 -->
					<!-- <router-link :to="`/home/message/detail?id=${info.id}&name=${info.name}&age=${info.age}`">{{info.name}}</router-link> -->
					
					<!-- query对象形式 -->
					<router-link :to="{
						path:'/home/message/detail',
						query:{
							id:info.id,
							name:info.name,
							age:info.age
						}
					}">
						{{info.name}}
					</router-link>
					<button @click="pushShow(info)">PUSH查看</button>
					<button @click="replaceShow(info)">Replace查看</button>
					<!-- params -->
					<!-- <router-link :to="`/home/message/detail/${info.id}/${info.name}/${info.age}`">{{info.name}}</router-link> -->
					<!-- params对象形式 name必须写，不能写path形式 -->
					<!-- <router-link :to="{
						name:'xiangqing',
						params:{
							id:info.id,
							name:info.name,
							age:info.age
						}
					}"> -->
				</li>
			</ul>
		</div>
		<div class="messageView">
			<router-view></router-view>
		</div>
	</div>
</template>

<script>
	export default {
		name:'Message',
		data() {
			return{
				userInfo:[
					{id:'1',name:'zhangsan',age:18},
					{id:'2',name:'lisi',age:10},
					{id:'3',name:'wangwu',age:15}
				]
			}
		},
		methods:{
			pushShow(info){
				this.$router.push({
					path:'/home/message/detail',
					query:{
						id:info.id,
						name:info.name,
						age:info.age
					}
				}).catch(err => {
					console.log('输出报错',err)
				})
			},
			replaceShow(info){
				this.$router.replace({
					path:'/home/message/detail',
					query:{
						id:info.id,
						name:info.name,
						age:info.age
					}
				}).catch(err => {
					console.log('输出报错',err)
				})
			},
			back(){
				this.$router.back(); 
			},
			forward(){
				this.$router.forward();
			}
		}
	}
</script>

<style>
	.messageList .active{background: #000;color: #fff;}
</style>
