<template>
	<div class="about_page">
		<h1>我是About</h1>
	</div>
</template>

<script>
	export default {
		name:'About',
		// 通过路由规则，进入该组件时被调用
		beforeRouteEnter(to,from,next) {
			if(to.meta.isAuth){
				if(localStorage.getItem('pomission') == 'true'){
					next();
				}else{
					alert('无权限查看');
					return false;
				}
			}else{
				next();
			}
		},
		// 通过路由规则，离开该组件时被调用
		beforeRouteLeave(to,from,next) {
			next();
		}
	}
</script>

