//该文件用于创建整个应用的路由器
import VueRouter from 'vue-router';

//引入组件
import Home from '../pages/Home.vue';
import About from '../pages/About.vue';
import News from '../pages/News.vue';
import Message from '../pages/Message.vue';
import Detail from '../pages/Detail.vue';

// export default new VueRouter({
const router =  new VueRouter({
	mode:'hash',//默认hash，路径有/#,history路径只有/---------两者之间的区别:1.hash兼容性好2.上线后hash路径不传值
	// 使用history 为防止刷新路径不存在报404 后端使用npm包(connect-history-api-fallback)或者nginx 修改
	routes:[
		{
			name:'guanyu',//路由命名
			path:'/about',
			component:About,
			meta:{isAuth:true,title:'关于'},
		},
		{
			name:'shouye',
			path:'/home',
			component:Home,
			meta:{title:'首页'},
			children:[//children的path 不加/
				{
					name:'xinwen',
					path:'news',
					component:News,
					meta:{isAuth:true,title:'新闻'},
					beforeEnter(to,from,next) {//独享前置路由守卫，只有前置，没有后置(使用afterEach即可)
						if(to.meta.isAuth){
							if(localStorage.getItem('pomission') == 'true'){
								next();
							}else{
								alert('无权限查看');
								return false;
							}
						}else{
							next();
						}
					}
				},
				{
					name:'xiaoxi',
					path:'message',
					component:Message,
					meta:{isAuth:true,title:'消息'},
					children:[
						{
							name:'xiangqing',
							path:'detail',
							// path:'detail/:id/:name/:age',//params写法
							component:Detail,
							// f1.对象
							// props:{id:1,name:'hello'}
							// f2.true,若布尔值为真，该路由组件收到的params参数，以props的形式传给Detail组件
							// props:true
							// f3.值为函数
							// props($route){
							// 	return {
							// 		id:$route.query.id,
							// 		name:$route.query.name,
							// 		age:$route.query.age,
							// 	}
							// }
							props({query}){
								return {
									id:query.id,
									name:query.name,
									age:query.age,
								}
							}
						}
					]
				},
			]
		},
	]
})

// // 全局前置路由守卫,初始化时被调用,路由切换前被调用
// router.beforeEach((to,from,next)=>{
// 	// 1.路径写法
// 	// if(to.path == '/home/news' || to.path == '/home/message'){
// 	// 2.name写法,必须配置name
// 	// if(to.name == 'xinwen' || to.name == 'xiaoxi'){
// 	// 3.设置meta:{isAuth:true}给需要设置权限的route
// 	if(to.meta.isAuth){
// 		if(localStorage.getItem('pomission') == 'true'){
// 			next();
// 		}else{
// 			alert('无权限查看');
// 			return false;
// 		}
// 	}else{
// 		next();
// 	}
// })

// 全局后置路由守卫,初始化时被调用，路由切换后被调用
router.afterEach((to,from,next)=>{
	// 当首页初始化加载时，to.meta.title为空，则需要添加 || '首页'
	document.title = to.meta.title || '首页'
})

export default router;